body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
  background-color: #fcfcfc;
}

hr {
  height: 1px;
  border: 0;
  background-color: #ccc;
  margin: 20px 0;
  padding: 0;
}

small {
  font-size: 11px;
  color: #666;
}

small a {
  color: inherit;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

.skills {
  margin: 0 0 30px 0;
  padding: 0;
  width: calc(100% - 50px);
}

.skills li {
  display: block;
  position: relative;
  background-color: #888;
  color: #fff;
  margin: 10px 0;
  transition: width 300ms ease-in-out;
}

.collapsed .skills li {
  width: 0 !important;
}

.skills p {
  padding: 10px;
  margin: 0;
  font-weight: bold;
}

.skills span {
  position: absolute;
  right: 10px;
  display: inline-block;
  width: 30px;
  top: 11px;
  text-align: right;
  font-weight: normal;
  color: #fff;
  font-size: 11px;
}
